<template>
  <div class="loginView">
    <form class="bd">
      <ul>
        <li><span>账号:</span><input type="text" v-model="userID" /></li>
        <li>
          <span>密码:</span><input type="password" v-model="userPassword" />
        </li>
        <li><div class="send" @click="login">确认登录</div></li>
        <li class="zc">
          <router-link to="/register">还没有账号前去注册</router-link>
        </li>
      </ul>
    </form>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      userID: "",
      userPassword: "",
    };
  },
  methods: {
    login() {
      if (this.userID.trim() != "" && this.userPassword.trim() != "") {
        alert("登录成功");
      }
    },
  },
};
</script>

